<script>
  /**
   * This file is named 000_Main.svelte, because CSS is added by component name alphabetical order
   * and we really need to have the rules defined here coming first!
   */

  import App from './App.svelte'

  /**
   * NOTES_ABOUT_STYLE:
   * TLDR;
   * In order to isolate the Devtools style from the main page style:
   * - Use generic html components (div, span) instead of specific components (header, footer, li, ul), as those are more
   *   likely to have very specific styling applied by the main page escaping the isolation explained below. (ex: body header:not(.header-checkout) {...})
   * - Always declare your css classes along with the html components it belongs to
   *    -> `div.footer { color: blue; }` instead of just `.footer { color: blue;}
   * - Apply style class names as '<dollar>.<className>' so that they are scoped by svelte-preprocess-cssmodules
   *   (replace <doller> with the actual character)
   *
   * Our main goal here is to isolate the Devtools CSS style from the app it is embedded in.
   * To achieve this we need to:
   * - A: Make sure that style inherited from parend nodes is not forwarded to Devtools (ex: a background color on the <body>)
   * - B: Make sure that style applied directly to html elements is not applied to Devtools' elements (ex: a style applied on <button>)
   * - C: Make sure that Devtools class names are unique enough to not conflict with the App's own style.
   *   Vice-versa, this also prevents the devtools CSS style from breaking the main app.
   *
   * To test the result locally, you can uncomment some test css in public/index.html
   *
   * - (A) all: initial on the wrapper clears any style that could have been inherited by parent element
   * - (B) all: unset on every html element (div, aside, ...) removes any style that would be globally assigned to
   *   that element. We use `unset` so that it inherits style from parent withing the Devtools, up to (A) wrapper.
   *
   * - (B) CSS Class selectors must always be attached to the HTML element they are applied to.
   *   ex: 'div.container' instead of just '.container'
   *   This is to make sure it has higher priority over the reset rule defined in (B)
   *
   * - (C) As much as possible we apply style to html elements directly as Svelte does a complete scoping on that (ie: it attaches its own scoped class only).
   *   When using classes as css selectors, Svelte is scoping the style definition but still exposes the original class name.
   *   For example, using a .root class to apply Svelte style, the component renders as <div class="root svelte-12345">
   *   which is likely to conflict with a global style applied to .root class.
   *   That explains why we use svelte-preprocess-cssmodules.
   *
   * - (C) We don't write dollar as a symbol in the next note line because it would be parsed by svelte-preprocess-cssmodules.
   *   Yes, it's a fragile library...
   *
   * - (C) Classes used as <dollar>.<class_name>  are replaced by file-scoped class names by svelte-preprocess-cssmodules
   *
   */
</script>

<div class="layer0-devtools-reset-style">
  <App />
</div>

<style>
  /*
      !Important: These :global() declaration need to be the first declaration of the generated CSS file
                  so that devtools custom style is applied on top of it, otherwise it would end up being unset too.
  */
  :global(.layer0-devtools-reset-style) {
    all: initial;
  }
  :global(.layer0-devtools-reset-style span, .layer0-devtools-reset-style a) {
    all: unset;
  }

  /* For some html element, we need to re-set browser default after unset */
  :global(.layer0-devtools-reset-style div, .layer0-devtools-reset-style
      aside, .layer0-devtools-reset-style header, .layer0-devtools-reset-style footer) {
    all: unset;
    display: block;
  }
  :global(.layer0-devtools-reset-style p) {
    all: unset;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
  }
  :global(.layer0-devtools-reset-style pre) {
    all: unset;
    display: block;
    font-family: monospace;
    white-space: pre;
    margin: 1em 0px;
  }
  :global(.layer0-devtools-reset-style button) {
    all: unset;
    display: inline-block;
    text-align: center;
    align-items: flex-start;
  }

  :global(.layer0-devtools-reset-style .layer0-devtools-bg-browser) {
    background-color: #4caf50;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-bg-prefetch) {
    background-color: #bdbdbd;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-bg-edge) {
    background-color: #ffeb3b;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-bg-server) {
    background-color: #f44336;
  }

  :global(.layer0-devtools-reset-style .layer0-devtools-color-browser) {
    color: #4caf50;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-color-prefetch) {
    color: #bdbdbd;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-color-edge) {
    color: #ffeb3b;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-color-server) {
    color: #f44336;
  }

  :global(.layer0-devtools-reset-style .layer0-devtools-color-success) {
    color: #1bb7c6;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-color-default) {
    color: #fff;
  }
  :global(.layer0-devtools-reset-style .layer0-devtools-color-failure) {
    color: #ca4ad9;
  }
</style>
