<script>
  import Ball from '../components/Ball.svelte'
  import CacheItemDetails from './CacheItemDetails.svelte'
  import CacheItemLine from './CacheItemLine.svelte'
  import { onMount } from 'svelte'
  import clsx from 'clsx'

  export let item
  export let index = 0
  export let animate = false

  /**
   * How to adjust ball placements:
   * - tweak .server, .browser and .edge initial translateY value to align with
   *   corresponding section
   * - adjust itemHeight to the height of each cache item line
   * - adjust marginTop to be the distant of the first cache item line from the top of the Drawer
   */
  const marginTop =
    3 * 39 /* the height of servereless, edge, browser */ +
    53 /* the header height */ +
    8 /* the top padding on cache items */
  const itemHeight = 26

  let drop = false
  let fade = false
  let done = !animate
  let showDetails = false

  function toggleDetails() {
    showDetails = !showDetails
  }

  const tier = item.source
  const ballColor = item.prefetch ? 'prefetch' : item.source

  // Limit the number of simultaneous falling balls to 15
  if (index >= 15) {
    drop = true
    done = true
  }

  if (animate) {
    onMount(() => {
      setTimeout(() => {
        fade = true
      }, 0)
      setTimeout(() => {
        drop = true
        setTimeout(() => {
          done = true
        }, 300)
      }, 100)
    })
  }
</script>

<!-- dropping ball -->
{#if !done}
  <div
    class={clsx({
      'drop-opEnGl': true,
      'edge-sbV9va': ['edge', 'edge-412'].includes(tier),
      'server-21lH8v': tier === 'server',
      'browser-3xodig': tier === 'browser',
      'hidden-12hTt2': done || !animate,
      'fade-2FAYBr': fade,
    })}
    style={drop ? `transform: translateY(${marginTop}px); top: ${(index + 1) * itemHeight}px` : ''}
  >
    <Ball color={ballColor} />
  </div>
{/if}

<!-- the the list item -->
<div
  class={clsx(['cache-item-391_8j', { 'done-2gwxKc': done, 'expanded-2MZ0Mx': showDetails }])}
  on:click={toggleDetails}
>
  <div class="ball-line-1gSq20">
    <Ball color={ballColor} />
    <div class={clsx(['text-2I3gce', { 'done-2gwxKc': done }])}>
      <CacheItemLine {item} />
    </div>
  </div>
  <div class={`details-W1SEgm${showDetails ? '' : ' hidden-12hTt2'}`}>
    <CacheItemDetails {item} />
  </div>
</div>

<style>
  /* !Important: read NOTES_ABOUT_STYLE in 000_Main.svelte */
  :global(div.cache-item-391_8j) {
    display: flex;
    flex-direction: column;
    padding: 4px 12px;
    /* flex-shrink:0 is important for Safari, which shrinks by default */
    flex-shrink: 0;
    cursor: pointer;
    opacity: 0;
    margin: 0 -12px;
    color: #e0e0e0;
    transition: background 100ms linear;
  }
  :global(div.cache-item-391_8j.done-2gwxKc) {
    opacity: 1;
  }
  :global(div.cache-item-391_8j.done-2gwxKc:hover) {
    background: #2e3b65;
  }
  :global(div.ball-line-1gSq20) {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  :global(li.expanded-2MZ0Mx,)
  :global(li.expanded-2MZ0Mx:hover) {
    background: rgba(25, 20, 35, 0.85) !important;
  }
  :global(div.details-W1SEgm) {
    color: white;
    font-size: 12px;
    line-height: 1.5em;
    border-left: 1px solid lightgrey;
    margin: 4px 0 4px 6px;
    padding-left: 5px;
  }
  :global(div.details-W1SEgm) > * {
    white-space: normal;
  }

  :global(div.details-W1SEgm.hidden-12hTt2) {
    display: none;
  }

  :global(div.text-2I3gce) {
    display: flex;
    overflow: hidden;
    flex: 1;
    opacity: 0;
    transition: opacity 150ms linear;
    align-items: center;
  }

  :global(div.drop-opEnGl) {
    position: absolute;
    transition: 300ms transform ease-in, 300ms top ease-out, 300ms opacity linear;
    opacity: 0;
    top: 0;
  }

  :global(div.fade-2FAYBr) {
    opacity: 1;
  }

  :global(div.hidden-12hTt2) {
    display: none;
  }

  :global(div.done-2gwxKc) {
    opacity: 1;
  }

  /* These control were the balls start falling from */
  :global(div.server-21lH8v) {
    transform: translateY(64px);
  }
  :global(div.edge-sbV9va) {
    transform: translateY(103px);
  }
  :global(div.browser-3xodig) {
    transform: translateY(142px);
  }
</style>
